"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { useDeleteJob, useJobsforAdmin } from "@/app/customHooks/useJobs";
import { JobI } from "@/app/utility/props";
import { Briefcase, MapPin, Trash2, Users, PlusCircle } from "lucide-react";
import JobForm from "./JobForm";

export default function JobsList() {
  const router = useRouter();
  const { data, isLoading, error } = useJobsforAdmin();
  const { mutate: deleteJob } = useDeleteJob();
  const [showForm, setShowForm] = useState(false);
  const [expandedJob, setExpandedJob] = useState<string | null>(null);

  const jobs = data as JobI[];

  if (isLoading)
    return (
      <p className="text-center text-gray-500 mt-10">Loading jobs...</p>
    );
  if (error)
    return (
      <p className="text-center text-red-500 mt-10">Failed to load jobs.</p>
    );

  return (
    <div className="bg-gray-50 min-h-screen p-8">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-gray-800 flex items-center gap-2">
          <Briefcase size={26} className="text-blue-600" />
          Manage Jobs
        </h1>
        <button
          onClick={() => setShowForm(true)}
          className="bg-blue-600 text-white px-4 py-2 rounded-lg flex items-center gap-2 hover:bg-blue-700 transition"
        >
          <PlusCircle size={18} /> Add Job
        </button>
      </div>

      {showForm && <JobForm onClose={() => setShowForm(false)} />}

      {/* Jobs Grid */}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {jobs?.map((job) => {
          const isExpanded = expandedJob === job._id;
          const shortDesc =
            job.description.length > 120
              ? `${job.description.slice(0, 120)}...`
              : job.description;

          return (
            <div
              key={job._id}
              className="bg-white border rounded-xl shadow-sm hover:shadow-md p-6 transition relative"
            >
              <div className="flex justify-between items-start">
                <div>
                  <h3 className="text-xl font-semibold text-gray-900 mb-1">
                    {job.title}
                  </h3>
                  <p className="text-sm text-gray-500">
                    {job.companyName}
                  </p>
                </div>
                <button
                  onClick={() => deleteJob(job._id)}
                  className="text-red-500 hover:text-red-700 transition"
                >
                  <Trash2 size={20} />
                </button>
              </div>

              <div className="flex items-center text-sm text-gray-600 mt-2 gap-3">
                <MapPin size={16} className="text-blue-600" />
                {job.location}
              </div>

              <p className="text-gray-700 mt-3 text-sm leading-relaxed">
                {isExpanded ? job.description : shortDesc}
                {job.description.length > 120 && (
                  <button
                    onClick={() =>
                      setExpandedJob(isExpanded ? null : job._id)
                    }
                    className="text-blue-600 ml-2 hover:underline"
                  >
                    {isExpanded ? "Show less" : "Read more"}
                  </button>
                )}
              </p>

              <div className="mt-4">
                <p className="text-sm text-gray-600">
                  <strong>Experience:</strong> {job.experience}
                </p>
                <p className="text-sm text-gray-600">
                  <strong>Salary:</strong> {job.salary}
                </p>
                <p className="text-sm text-gray-600">
                  <strong>Openings:</strong> {job.openings}
                </p>
                <p className="text-sm text-gray-600 mt-1">
                  <strong>Skills:</strong>{" "}
                  <span className="text-gray-800">
                    {job.keySkills.join(", ")}
                  </span>
                </p>
              </div>

              <div className="flex justify-between items-center mt-5">
                <button
                  onClick={() => router.push(`/admin/applications/${job._id}`)}
                  className="bg-green-600 text-white text-sm px-4 py-2 rounded-md flex items-center gap-2 hover:bg-green-700 transition"
                >
                  <Users size={16} /> View Applicants
                </button>
                <span className="text-xs text-gray-400">
                  Posted on {new Date(job.createdAt!).toLocaleDateString()}
                </span>
              </div>
            </div>
          );
        })}
      </div>

      {jobs?.length === 0 && (
        <p className="text-center text-gray-500 mt-10">
          No jobs available. Click "Add Job" to create one.
        </p>
      )}
    </div>
  );
}
